"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Separator } from "@/components/ui/separator";
import { toast } from "sonner";
import Link from "next/link";

interface StoreOutfit {
  id: string;
  name: string;
  category: string;
  price: number;
  originalPrice?: number;
  brand: string;
  image: string;
  rating: number;
  reviews: number;
  description: string;
  features: string[];
  affiliateLinks: {
    amazon?: string;
    myntra?: string;
    ajio?: string;
    meesho?: string;
  };
  discount?: number;
  trending?: boolean;
  newArrival?: boolean;
}

const storeOutfits: StoreOutfit[] = [
  {
    id: "store-shirt-1",
    name: "Premium Cotton Formal Shirt",
    category: "shirts",
    price: 1299,
    originalPrice: 1599,
    brand: "StyleCraft",
    image: "https://placehold.co/400x500?text=Premium+Cotton+Formal+Shirt",
    rating: 4.5,
    reviews: 234,
    description: "Premium quality cotton formal shirt perfect for office and formal occasions.",
    features: ["100% Cotton", "Wrinkle Free", "Easy Care", "Slim Fit"],
    affiliateLinks: {
      amazon: "#",
      myntra: "#"
    },
    discount: 19,
    trending: true
  },
  {
    id: "store-shirt-2",
    name: "Casual Denim Shirt",
    category: "shirts",
    price: 1599,
    originalPrice: 1999,
    brand: "DenimCo",
    image: "https://placehold.co/400x500?text=Casual+Denim+Shirt",
    rating: 4.3,
    reviews: 156,
    description: "Stylish casual denim shirt for everyday wear.",
    features: ["Premium Denim", "Comfortable Fit", "Durable", "Versatile"],
    affiliateLinks: {
      amazon: "#",
      ajio: "#"
    },
    discount: 20,
    newArrival: true
  },
  {
    id: "store-pant-1",
    name: "Formal Black Trousers",
    category: "pants",
    price: 1899,
    originalPrice: 2299,
    brand: "FormalWear",
    image: "https://placehold.co/400x500?text=Formal+Black+Trousers",
    rating: 4.6,
    reviews: 189,
    description: "Classic formal trousers for professional look.",
    features: ["Wrinkle Resistant", "Comfortable Fit", "Premium Fabric", "Easy Care"],
    affiliateLinks: {
      myntra: "#",
      meesho: "#"
    },
    discount: 17
  },
  {
    id: "store-kurta-1",
    name: "Traditional Cotton Kurta",
    category: "kurta",
    price: 999,
    originalPrice: 1299,
    brand: "EthnicWear",
    image: "https://placehold.co/400x500?text=Traditional+Cotton+Kurta",
    rating: 4.4,
    reviews: 267,
    description: "Comfortable traditional cotton kurta for festivals and casual wear.",
    features: ["Pure Cotton", "Breathable", "Traditional Design", "Comfortable"],
    affiliateLinks: {
      amazon: "#",
      myntra: "#"
    },
    discount: 23,
    trending: true
  },
  {
    id: "store-saree-1",
    name: "Elegant Silk Saree",
    category: "saree",
    price: 2999,
    originalPrice: 3999,
    brand: "SilkHouse",
    image: "https://placehold.co/400x500?text=Elegant+Silk+Saree",
    rating: 4.7,
    reviews: 145,
    description: "Beautiful silk saree perfect for special occasions and festivals.",
    features: ["Pure Silk", "Rich Colors", "Traditional Weave", "Elegant Design"],
    affiliateLinks: {
      amazon: "#",
      myntra: "#",
      ajio: "#"
    },
    discount: 25,
    newArrival: true
  },
  {
    id: "store-shoes-1",
    name: "Leather Formal Shoes",
    category: "shoes",
    price: 2499,
    originalPrice: 2999,
    brand: "FootCraft",
    image: "https://placehold.co/400x500?text=Leather+Formal+Shoes",
    rating: 4.5,
    reviews: 198,
    description: "Premium leather formal shoes for professional and formal occasions.",
    features: ["Genuine Leather", "Comfortable Sole", "Durable", "Classic Design"],
    affiliateLinks: {
      amazon: "#",
      myntra: "#"
    },
    discount: 17
  }
];

export default function StorePage() {
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [sortBy, setSortBy] = useState("featured");
  const [priceRange, setPriceRange] = useState("all");
  const [selectedOutfit, setSelectedOutfit] = useState<StoreOutfit | null>(null);

  const categories = [
    { id: "all", name: "All Categories" },
    { id: "shirts", name: "Shirts" },
    { id: "pants", name: "Pants" },
    { id: "kurta", name: "Kurta" },
    { id: "saree", name: "Saree" },
    { id: "shoes", name: "Shoes" },
    { id: "accessories", name: "Accessories" }
  ];

  const filteredOutfits = storeOutfits.filter(outfit => {
    const matchesSearch = outfit.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         outfit.brand.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesCategory = selectedCategory === "all" || outfit.category === selectedCategory;
    
    let matchesPrice = true;
    if (priceRange !== "all") {
      const price = outfit.price;
      switch (priceRange) {
        case "under-1000":
          matchesPrice = price < 1000;
          break;
        case "1000-2000":
          matchesPrice = price >= 1000 && price <= 2000;
          break;
        case "2000-3000":
          matchesPrice = price >= 2000 && price <= 3000;
          break;
        case "above-3000":
          matchesPrice = price > 3000;
          break;
      }
    }

    return matchesSearch && matchesCategory && matchesPrice;
  });

  const sortedOutfits = [...filteredOutfits].sort((a, b) => {
    switch (sortBy) {
      case "price-low":
        return a.price - b.price;
      case "price-high":
        return b.price - a.price;
      case "rating":
        return b.rating - a.rating;
      case "newest":
        return b.newArrival ? 1 : -1;
      default:
        return 0;
    }
  });

  const addToWishlist = (outfit: StoreOutfit) => {
    toast.success(`${outfit.name} added to wishlist!`);
  };

  const buyNow = (outfit: StoreOutfit, platform: string) => {
    toast.success(`Redirecting to ${platform} for ${outfit.name}`);
  };

  const tryOnOutfit = (outfit: StoreOutfit) => {
    toast.success(`Added ${outfit.name} to try-on selection!`);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white">
      {/* Header */}
      <header className="border-b bg-white/80 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <Link href="/" className="flex items-center space-x-2">
                <div className="w-8 h-8 bg-gradient-to-r from-purple-600 to-pink-600 rounded-lg"></div>
                <h1 className="text-2xl font-bold bg-gradient-to-r from-purple-600 to-pink-600 bg-clip-text text-transparent">
                  AI Try-On
                </h1>
              </Link>
            </div>
            <nav className="hidden md:flex items-center space-x-6">
              <Link href="/" className="text-gray-700 hover:text-purple-600 font-medium">
                Home
              </Link>
              <Link href="/try-on" className="text-gray-700 hover:text-purple-600 font-medium">
                Try-On
              </Link>
              <Link href="/store" className="text-purple-600 font-medium">
                Store
              </Link>
              <Link href="/profile" className="text-gray-700 hover:text-purple-600 font-medium">
                Profile
              </Link>
            </nav>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {/* Page Header */}
        <div className="text-center mb-8">
          <h2 className="text-4xl font-bold text-gray-900 mb-4">
            Fashion Store
          </h2>
          <p className="text-xl text-gray-600">
            Discover and shop the latest fashion trends
          </p>
        </div>

        {/* Filters and Search */}
        <Card className="mb-8">
          <CardContent className="p-6">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <div>
                <label className="text-sm font-medium mb-2 block">Search</label>
                <Input
                  placeholder="Search outfits..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
              </div>
              
              <div>
                <label className="text-sm font-medium mb-2 block">Category</label>
                <Select value={selectedCategory} onValueChange={setSelectedCategory}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {categories.map((category) => (
                      <SelectItem key={category.id} value={category.id}>
                        {category.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div>
                <label className="text-sm font-medium mb-2 block">Price Range</label>
                <Select value={priceRange} onValueChange={setPriceRange}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Prices</SelectItem>
                    <SelectItem value="under-1000">Under ₹1,000</SelectItem>
                    <SelectItem value="1000-2000">₹1,000 - ₹2,000</SelectItem>
                    <SelectItem value="2000-3000">₹2,000 - ₹3,000</SelectItem>
                    <SelectItem value="above-3000">Above ₹3,000</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div>
                <label className="text-sm font-medium mb-2 block">Sort By</label>
                <Select value={sortBy} onValueChange={setSortBy}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="featured">Featured</SelectItem>
                    <SelectItem value="price-low">Price: Low to High</SelectItem>
                    <SelectItem value="price-high">Price: High to Low</SelectItem>
                    <SelectItem value="rating">Highest Rated</SelectItem>
                    <SelectItem value="newest">Newest First</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Results Count */}
        <div className="mb-6">
          <p className="text-gray-600">
            Showing {sortedOutfits.length} of {storeOutfits.length} products
          </p>
        </div>

        {/* Products Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
          {sortedOutfits.map((outfit) => (
            <Card key={outfit.id} className="group hover:shadow-lg transition-all duration-300">
              <CardContent className="p-0">
                <div className="relative">
                  <div className="aspect-[3/4] rounded-t-lg overflow-hidden">
                    <img 
                      src={outfit.image} 
                      alt={outfit.name}
                      className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                      onError={(e) => {
                        e.currentTarget.src = `https://placehold.co/400x500?text=${outfit.name}`;
                      }}
                    />
                  </div>
                  
                  {/* Badges */}
                  <div className="absolute top-2 left-2 flex flex-col space-y-1">
                    {outfit.discount && (
                      <Badge className="bg-red-500 text-white">
                        {outfit.discount}% OFF
                      </Badge>
                    )}
                    {outfit.trending && (
                      <Badge className="bg-orange-500 text-white">
                        Trending
                      </Badge>
                    )}
                    {outfit.newArrival && (
                      <Badge className="bg-green-500 text-white">
                        New
                      </Badge>
                    )}
                  </div>

                  {/* Wishlist Button */}
                  <Button
                    size="sm"
                    variant="outline"
                    className="absolute top-2 right-2 w-8 h-8 p-0 bg-white/80 hover:bg-white"
                    onClick={() => addToWishlist(outfit)}
                  >
                    ♡
                  </Button>
                </div>

                <div className="p-4 space-y-3">
                  <div>
                    <h3 className="font-semibold text-lg line-clamp-2">{outfit.name}</h3>
                    <p className="text-sm text-gray-600">{outfit.brand}</p>
                  </div>

                  <div className="flex items-center space-x-2">
                    <div className="flex items-center">
                      <span className="text-yellow-400">★</span>
                      <span className="text-sm font-medium ml-1">{outfit.rating}</span>
                    </div>
                    <span className="text-sm text-gray-500">({outfit.reviews})</span>
                  </div>

                  <div className="flex items-center space-x-2">
                    <span className="text-xl font-bold">₹{outfit.price}</span>
                    {outfit.originalPrice && (
                      <span className="text-sm text-gray-500 line-through">
                        ₹{outfit.originalPrice}
                      </span>
                    )}
                  </div>

                  <div className="flex space-x-2">
                    <Button 
                      size="sm" 
                      variant="outline" 
                      className="flex-1"
                      onClick={() => tryOnOutfit(outfit)}
                    >
                      Try On
                    </Button>
                    <Dialog>
                      <DialogTrigger asChild>
                        <Button 
                          size="sm" 
                          className="flex-1 bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700"
                          onClick={() => setSelectedOutfit(outfit)}
                        >
                          Buy Now
                        </Button>
                      </DialogTrigger>
                      <DialogContent className="max-w-2xl">
                        {selectedOutfit && (
                          <>
                            <DialogHeader>
                              <DialogTitle>{selectedOutfit.name}</DialogTitle>
                              <DialogDescription>
                                Choose your preferred shopping platform
                              </DialogDescription>
                            </DialogHeader>
                            
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                              <div>
                                <img 
                                  src={selectedOutfit.image} 
                                  alt={selectedOutfit.name}
                                  className="w-full rounded-lg"
                                  onError={(e) => {
                                    e.currentTarget.src = `https://placehold.co/400x500?text=${selectedOutfit.name}`;
                                  }}
                                />
                              </div>
                              
                              <div className="space-y-4">
                                <div>
                                  <h4 className="font-semibold text-lg">{selectedOutfit.name}</h4>
                                  <p className="text-gray-600">{selectedOutfit.brand}</p>
                                </div>

                                <div className="flex items-center space-x-2">
                                  <span className="text-2xl font-bold">₹{selectedOutfit.price}</span>
                                  {selectedOutfit.originalPrice && (
                                    <span className="text-lg text-gray-500 line-through">
                                      ₹{selectedOutfit.originalPrice}
                                    </span>
                                  )}
                                </div>

                                <p className="text-gray-700">{selectedOutfit.description}</p>

                                <div>
                                  <h5 className="font-medium mb-2">Features:</h5>
                                  <ul className="text-sm text-gray-600 space-y-1">
                                    {selectedOutfit.features.map((feature, index) => (
                                      <li key={index}>• {feature}</li>
                                    ))}
                                  </ul>
                                </div>

                                <Separator />

                                <div>
                                  <h5 className="font-medium mb-3">Buy from:</h5>
                                  <div className="grid grid-cols-2 gap-2">
                                    {selectedOutfit.affiliateLinks.amazon && (
                                      <Button 
                                        onClick={() => buyNow(selectedOutfit, "Amazon")}
                                        className="bg-orange-500 hover:bg-orange-600"
                                      >
                                        Amazon
                                      </Button>
                                    )}
                                    {selectedOutfit.affiliateLinks.myntra && (
                                      <Button 
                                        onClick={() => buyNow(selectedOutfit, "Myntra")}
                                        className="bg-pink-500 hover:bg-pink-600"
                                      >
                                        Myntra
                                      </Button>
                                    )}
                                    {selectedOutfit.affiliateLinks.ajio && (
                                      <Button 
                                        onClick={() => buyNow(selectedOutfit, "Ajio")}
                                        className="bg-yellow-500 hover:bg-yellow-600"
                                      >
                                        Ajio
                                      </Button>
                                    )}
                                    {selectedOutfit.affiliateLinks.meesho && (
                                      <Button 
                                        onClick={() => buyNow(selectedOutfit, "Meesho")}
                                        className="bg-red-500 hover:bg-red-600"
                                      >
                                        Meesho
                                      </Button>
                                    )}
                                  </div>
                                </div>
                              </div>
                            </div>
                          </>
                        )}
                      </DialogContent>
                    </Dialog>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {sortedOutfits.length === 0 && (
          <div className="text-center py-12">
            <p className="text-gray-500 text-lg">No products found matching your criteria.</p>
            <Button 
              onClick={() => {
                setSearchQuery("");
                setSelectedCategory("all");
                setPriceRange("all");
              }}
              className="mt-4"
            >
              Clear Filters
            </Button>
          </div>
        )}
      </div>
    </div>
  );
}
